.todoListMain {
    font-family: sans-serif;
    position: relative;
    color: rgb(0, 40, 90);
    padding: 2px 32px;
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(221, 221, 221, 0.75);
    display: block;
    /*height: 420px;*/
    height: 72vh;
    overflow-y: scroll;
    -webkit-transition: all 2s ease-in-out;
    transition: all 2s ease-in-out;
}

.add-post {
    margin: 0 auto;
    padding: 0;
}

.add-post-title {
    position: relative;
    color: black;
    margin: 16px 0;
    padding: 0 16px;
    margin-left: 0;
    text-align: left;
    font-weight: 400;
}

.btn-add-post {
    float: right;
    margin: 0;
    padding: 8px 16px;
    background: rgba(51, 153, 184, 0.747);
    border: rgba(51, 153, 184, 0.747);
    color: white;
}

.btn-add-post:hover {
    background-color: #003399;
    cursor: pointer;
}

.hide {   
    -webkit-transition: all 2s ease-in-out;   
    transition: all 2s ease-in-out;
    display: none;
}

.visible {    
    transition: all 2s ease-in-out;
    -webkit-transition: all 2s ease-in-out;
    display: block;
}

.todoListMain .header input {
    padding: 10px;
    font-size: 14px;
    border: 2px solid #003399;
    width: 100%;
}

.todoListMain .header .btn-public {
    text-align: center;
    margin: 10px auto;
    padding: 10px;
    font-size: 14px;
    background-color: #0066FF;
    color: #FFF;
    border: 2px solid #0066FF;
}

.todoListMain .header .btn-public:hover {
    background-color: #003399;
    border: 2px solid #003399;
    cursor: pointer;
}

.todoListMain .theList {
    list-style: none;
    padding-left: 0;
    width: 100%;
}

.todoListMain .theList li {
    color: #333;
    background-color: inherit rgba(51, 184, 133, 0.747);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    list-style: none;
}

ul.theList {
    padding: 0;
}

.textarea {
    font-family: sans-serif;
    width: 100%;
    height: 80px;
    border: 2px solid #003399;
    resize: none;
}

.btn-delete {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 2px 4px;
    background: none;
    border: none;
    color: red;
    cursor: pointer;
}

.hide {
    display: none;
}

.btn-delete .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: -25%;
    right: 120%;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.btn-delete .tooltiptext::after {
    content: "";
    position: absolute;
    top: 25%;
    left: 103%;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.btn-delete:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

@media screen and (max-width:768px) {
    .todoListMain {
        /*
        height: 520px;
        */
        height: 80vh;
        padding: 2px 16px;
    }
    .todoListMain .theList li {
        padding: 16px 0 32px 0;
    } 
    .col-10 {
        width: 83.33333%
    }

}
