.Post {
    /*background-image: url("img/header2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 0;    
    overflow-x: hidden;    
    */
    display: block;
    position: relative;
    box-sizing: border-box;
    padding: 0 16px 32px 16px;
    margin: 0 auto;
    font-family: sans-serif;
    position: relative;
    color: rgb(0, 40, 90);
}

.flex-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.flex-container>.flex-items {
    width: 100%;
    margin: 0;
    padding: 8px;
    text-align: center;
}

.post-title {
    position: relative;
    color: black;
    margin: 0;
    padding: 0 16px;
    margin-left: 0;
    text-align: left;
    font-weight: 400;
}

.avatar {
    /*
    margin: 0 0 8px 0;
    */
    max-width: 100%;
    height: 240px;
    padding: 8px;
    border-radius: 10px;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.788);
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.post-text {
    position: inherit;
    color: rgb(0, 40, 90);
    text-align: justify;
    margin: 16px 0 16px 0;
    font-size: 14px;
    line-height: 1.6;
}

.post-autor {
    padding: 4px 0;
    border-top: 0.15rem solid black;
    border-bottom: 0.1rem solid black;
    background-color: rgba(238, 238, 238, 0.788);
    text-align: left;
    display: flex;
    flex-direction: row;
    color: rgb(54, 54, 54);
}

.autor,
.date {
    margin: 0;
    font-weight: inherit;
    padding: 8px 16px;
    vertical-align: middle;
}

@media screen and (max-width:768px) {
    .Post {        
        padding: 0 0 32px 0;
        margin: 0 auto;
    }
    .flex-container {
        flex-direction: column;
    }

}
