/* slider */

body,
html {
    height: 100%;
    margin: 0;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
}

* {
    box-sizing: border-box;
}

.slider-wrapper {
    padding: 0;
    margin: 0;
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    background-size: cover;
}

.previousButton,
.nextButton {
    display: none;
    cursor: pointer;
}

.previousButton:hover,
.nextButton:hover {}
